@import "variables";
@import "mixin";
@import "deliveryPopup";
@import "fixed_nav";
@import "authorization";
@import "aside_buttons";

body {
  font-size: 20px;
  line-height: 22px;
  color: #474747;
  .header {
    width: 100%;
    .containerParent();
    .container {
      .containerChild();
      display: grid;
      grid-template-areas: ". headline headline" "logo search cabinet" "navPanel navPanel navPanel";
      grid-template-columns: 25% 1fr 25%;
      .header__headline {
        grid-area: headline;
        p {
          font: 1em/2em @sansregular;
          color: #474747;
          text-align: center;
          background-color: #ececec;
          border-bottom-right-radius: 15px;
          border-bottom-left-radius: 15px;
        }
      }
      .header__logo {
        grid-area: logo;
        a{
          margin-left: 50px;
          display: block;
          width: 100%;
          height: 100%;
          background: url("../img/logo.png") no-repeat;
          background-size: contain;
        }
      }
      .header__mainSearch {
        grid-area: search;
        display: flex;
        flex-wrap: wrap;
        .search {
          width: 100%;
          margin-top: 70px;
          input {
            width: 90%;
            height: 45px;
            padding-left: 25px;
            font: 1.125em/45px @sansregular;
            color: #9e9e9e;
            border: 3px solid @mainColor;
            border-radius: 50px;
            outline: none;
          }
          i {
            font-size: 1.5em;
            position: relative;
            left: -50px;
            top: 2px;
            color: @mainColor;
          }
        }
        .numbers {
          margin-top: 50px;
          ul {
            display: flex;
            li {
              padding: 0 10px;
              i {
                display: flex;
                justify-content: center;
                align-items: center;
                width: 65px;
                height: 65px;
                font-size: 2em;
                color: @mainColor;
                border: 3px solid @mainColor;
                border-radius: 50px;
              }
              a {
                font: 1.6em/2em @sansregular;
                color: #474747;
                text-decoration: none;
              }
            }
          }
        }
        .workTime {
          margin: 55px 0 0 60px;
          display: flex;
          i {
            font-size: 1.5em;
            line-height: 1.725em;
            color: @mainColor;
          }
          ul {
            margin-left: 20px;
            li {
              span {
                font: .7em/1em @sansregular;
              }
            }
          }
        }
      }
      .header__cabinet {
        grid-area: cabinet;
        margin-top: 70px;
        ul {
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
          li {
            a {
              display: flex;
              justify-content: space-around;
              align-content: center;
              text-decoration: none;
              span {
                display: inline-block;
                font: 1.125em/1.1em @sansregular;
                color: #474747;
                text-align: center;
                i {
                  padding: 0 20px;
                  font-size: 2em;
                  color: @mainColor;
                }
                &:hover {
                  text-decoration: underline;
                  cursor: pointer;
                }
              }
            }
            &:nth-child(1) {
              width: 45%;
            }
            &:nth-child(2) {
              width: 45%;
            }
            &:nth-child(3) {
              a{
                margin-top: 50px;
                width: 330px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                font: 1.125em/1.1em @sansregular;
                color: #474747;
                text-align: center;
                text-decoration: none;
                border: 1px solid @mainColor;
                border-radius: 5px;
                span{
                  margin: 5px 10px;
                }
                i{
                  padding: 3px;
                  font-size: 2em;
                  color: #fff;
                  background-color: #7d3daf;
                  border-radius: 5px;
                }
              }
            }
          }
        }
      }
      .header__nav-panel {
        grid-area: navPanel;
        &__major {
          ul {
            display: flex;
            justify-content: space-between;
            margin-top: 50px;
            li {
              width: 16.67%;
              a {
                display: inline-block;
                width: 100%;
                font: 1.125em/2em @sansbold;
                color: #fff;
                text-align: center;
                text-decoration: none;
                background-color: #176975;
                border-right: 1px solid #77c9cc;
                border-left: 1px solid #77c9cc;
                &:hover {
                  background-color: @hoverColor;
                }
              }
            }
          }
        }
        &__goods {
          ul {
            display: flex;
            justify-content: space-between;
            li {
              width: 25%;
              .dropMenu {
                width: 1150px;
                position: absolute;
                z-index: 10;
                padding: 0 25px 25px 0;
                display: flex;
                flex-wrap: wrap;
                background-color: @hoverColor;
                li {
                  margin: 20px 0 0 25px;
                  width: 30%;
                  display: flex;
                  flex-direction: column;
                  a {
                    color: #fff;
                    text-align: left;
                    border: none;
                    background-color: @hoverColor;
                    &:hover {
                      text-decoration: underline;
                    }
                  }
                  &:nth-child(1) {
                    font: 1em/1.125em @sansbold;
                    i {
                      line-height: 1.125em;
                    }
                  }
                  ul {
                    display: flex;
                    flex-direction: column;
                    li {
                      margin: 0 0 0 25px;
                      width: 100%;
                      a {
                        font: 1em/1.125em @sansregular;
                      }
                    }
                  }
                }
              }
              a {
                display: inline-block;
                width: 100%;
                font: 1.125em/2em @sansregular;
                color: @linkColor;
                text-align: center;
                text-decoration: none;
                background-color: #77c9cc;
                border: 1px solid @mainColor;
                &:hover {
                  color: #fff;
                  background-color: @hoverColor;
                }
              }
            }
          }
        }
      }
    }
  }
  .content {
    padding-top: 25px;
    width: 100%;
    .containerParent();
    .container {
      .containerChild();
      display: grid;
      grid-template-areas: "aside breadcrumb breadcrumb"
                           "aside productImg descr"
                           "aside analogue analogue"
                           "recomend recomend recomend"
                           "order order order";
      grid-template-columns: 25% 1fr 30%;
      .content__aside {
        grid-area: aside;
        width: 90%;
        div{
          background-color: @fontColor;
          a{
            padding-left: 15px;
          }
          h5{
            padding-left: 15px;
            height: 60px;
            font: 1.125em/60px @sansbold;
            color: #fff;
            &:hover{
              cursor: pointer;
            }
          }
          ul{
            height: 300px;
            overflow: auto;
            overflow-x: hidden;
            background-color: #77c9cc;
            li{
              padding: 5px 25px;
              a{
                padding-left: 0;
                font: 1em/1.125em @sansregular;
                color: @linkColor;
                text-decoration: none;
                &:hover{
                  text-decoration: underline;
                }
              }
              &:nth-child(3n+1){
                a{
                  font-family: @sansbold;
                }
              }
            }
          }
          .active{
            background-color: @linkColor;
          }
          &:first-child{
            border-top-left-radius: 10px;
            border-top-right-radius: 10px;
          }
          &:last-child{
            border-bottom-left-radius: 10px;
            border-bottom-right-radius: 10px;
          }
        }
        .sale{
          background-color: @hoverColor;

        }
        .sale,
        .catalog{
          a{
            font: 1.125em/60px @sansbold;
            color: #fff;
            text-decoration: none;
          }
        }
      }
      .content__productCard__breadcrumb {
        grid-area: breadcrumb;
        ul {
          display: flex;
          flex-wrap: wrap;
          li {
            a {
              display: inline-block;
              font: .875em/1em @sansregular;
              color: #474747;
            }
            span {
              font: .875em/1em @sansregular;
              color: #474747;
            }
          }
        }
      }
      .content__productCard__img{
        grid-area: productImg;
        h5{
          font: 2.25em/2.375em @serifitalic;
          color: @linkColor;
        }
        ul{
          display: flex;
          flex-wrap: wrap;
          li{
            margin: 10px;
            padding: 30px 10px;
            display: flex;
            justify-content: center;
            align-items: center;
            border: 2px solid #e6e6e6;
            border-radius: 15px;
            &:hover{
              cursor: pointer;
            }
          }
          .mainImg{
            width: 90%;
            padding: 90px 30px;
            img{
              width: 100%;
            }
          }
        }
      }
      .content__productCard__descr{
        grid-area: descr;
        margin-top: 105px;
        display: flex;
        flex-direction: column;
        .packaging,
        .plate{
          margin: 10px 0;
          padding: 25px;
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
          align-items: center;
          border: 2px solid #e6e6e6;
          border-radius: 15px;
          .value{
            margin-bottom: 25px;
            width: 100%;
            font: 1em/1.125em @sansregular;
            color: @linkColor;
            i{
              color: @mainColor;
            }
          }
          .price{
            width: 30%;
            font: 1.375em/1.5em @sansbold;
            color: @linkColor;
            span{
              font: .75em/.875em @sansregular;
            }
          }
          button{
            width: 125px;
            height: 40px;
            font: .875em/40px @sansregular;
            color: #fff;
            text-align: center;
            text-transform: uppercase;
            background-color: @mainColor;
            border: none;
            border-radius: 5px;
            outline: none;
            &:hover{
              cursor: pointer;
            }
          }
          .packagingQuantitu,
          .plateQuantitu{
            display: flex;
            align-items: center;
            span{
              display: inline-block;
              width: 25px;
              height: 40px;
              font: 1.375em/40px @sansregular;
              text-align: center;
              color: @linkColor;
              background-color: #77c9cc;
              user-select: none;
              &:hover{
                background-color: @mainColor;
                cursor: pointer;
              }
            }
            .minus{
              border-top-left-radius: 5px;
              border-bottom-left-radius: 5px;
            }
            .plus{
              border-top-right-radius: 5px;
              border-bottom-right-radius: 5px;
            }
            input{
              width: 35px;
              height: 34px;
              font-size: 1em;
              font-family: @sansregular;
              text-align: center;
              color: @linkColor;
              border: 2px solid #77c9cc;
              outline: none;
            }
          }
        }
        .others{
          margin: 10px 0;
          width: 100%;
          height: 60px;
          border: 2px solid #e6e6e6;
          border-radius: 15px;
          p{
            font: 1em/60px @sansbold;
            color: @mainColor;
            text-align: center;
            border-radius: 15px;
            &:hover{
              color: #fff;
              background-color: @mainColor;
              cursor: pointer;
            }
          }
        }
        .productInfo{
          display: flex;
          div{
            display: flex;
            flex-direction: column;
            p{
              margin: 10px 0;
              font: 1em/1.125em @sansbold;
              color: @linkColor;
            }
            span{
              margin: 10px 0;
              font: 1em/1.125em @sansregular;
              color: @linkColor;
            }
          }
        }
        .consultation{
          a{
            margin-top: 35px;
            width: 290px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            font: 1.125em/1.1em @sansregular;
            color: #474747;
            text-align: center;
            text-decoration: none;
            border: 1px solid @mainColor;
            border-radius: 5px;
            span{
              margin: 5px 10px;
            }
            i{
              padding: 3px;
              font-size: 2em;
              color: #fff;
              background-color: #7d3daf;
              border-radius: 5px;
            }
          }
        }
      }
      .content__productCard__analogue{
        grid-area: analogue;
        margin-top: 40px;
        .productCard__descr{
          display: flex;
          li{
            margin: 0 5px;
            width: 130px;
            text-align: center;
            border: 1px solid #e6e6e6;
            border-top-left-radius: 15px;
            border-top-right-radius: 15px;
            a{
              display: inline-block;
              font: 1.125em/50px @sansbold;
              color: @linkColor;
              text-decoration: none;
            }
            &:nth-child(3){
              background-color: #77c9cc;
            }
          }
        }
        .therapeuticEffect{
          padding: 0 0 35px 20px;
          border: 1px solid #e6e6e6;
          border-radius: 15px;
          .completeAnalogue,
          .otherReleseForm,
          .similarEffect{
            display: flex;
            flex-wrap: wrap;
            h6{
              margin: 30px 10px;
              width: 35%;
              font: 1.125em/1.25em @sansbold;
              color: @mainColor;
            }
            span{
              margin: 30px 0;
              width: 60%;
              font: .875em/1em @sansregular;
              color: #252525;
              text-align: right;
            }
            ul{
              display: flex;
              flex-wrap: wrap;
              li {
                margin: 10px 10px;
                text-align: center;
                border: 1px solid #e6e6e6;
                border-radius: 5px;
                a {
                  display: block;
                  text-decoration: none;
                  img {
                    margin-top: 15px;
                    width: 200px;
                    height: 200px;
                  }
                  p {
                    padding: 10px;
                    font: 1em/1.125em @sansregular;
                    color: @linkColor;
                    text-align: left;
                  }
                  .price {
                    font: 1.25em/1.375em @sansbold;
                    span {
                      font: .875em/1em @sansregular
                    }
                  }
                  &:hover {
                    border-color: #176975;
                    cursor: pointer;
                  }
                }
                &:hover {
                  border-color: #176975;
                  cursor: pointer;
                }
              }
            }
          }
        }
      }
      .content__recomend{
        grid-area: recomend;
        margin: 25px 0 25px 0;
        h5 {
          font: 2.25em/2.375em @serifitalic;
          color: @linkColor;
        }
        .slider{
          width: 100%;
          height: 350px;
          .lSSlideOuter {
            ul {
              .item-a,
              .item-b,
              .item-c,
              .item-e,
              .item-d,
              .item-f,
              .item-g,
              .item-h{
                width: 250px;
                height: 350px;
              }
              li {
                text-align: center;
                border: 1px solid #e6e6e6;
                border-radius: 5px;
                a {
                  display: block;
                  text-decoration: none;
                  img {
                    margin-top: 15px;
                    width: 200px;
                    height: 200px;
                  }
                  p {
                    padding: 10px;
                    font: 1em/1.125em @sansregular;
                    color: @linkColor;
                    text-align: left;
                  }
                  .price {
                    font: 1.25em/1.375em @sansbold;
                    span {
                      font: .875em/1em @sansregular
                    }
                  }
                  &:hover {
                    border-color: #176975;
                    cursor: pointer;
                  }
                }
                &:hover{
                  border-color: @fontColor;
                }
              }
            }
            .lSAction {
              a {
                background-image: none;
              }
            }
            .cS-hidden {
              height: 1px;
              opacity: 0;
              filter: alpha(opacity=0);
              overflow: hidden;
            }
          }
        }
      }
      .content__howToMakeOrder {
        grid-area: order;
        margin: 60px 0;
        display: flex;
        justify-content: space-between;
        div {
          width: 32.5%;
          height: 235px;
          display: flex;
          flex-wrap: wrap;
          background-color: #e6e6e6;
          border-radius: 5px;
          .icons {
            width: 40%;
            display: flex;
            justify-content: flex-end;
            i {
              font-size: 2em;
              color: #1b6b77;
            }
            .arrow {
              margin-top: 30px;
              width: 20%;
              text-align: right;
            }
            .cart,
            .box,
            .truck {
              margin-top: 40px;
              width: 100px;
              height: 100px;
              display: flex;
              justify-content: center;
              align-items: center;
              border: 2px solid #1b6b77;
              border-radius: 75px;
            }
          }
          .learnMore {
            width: 60%;
            display: flex;
            justify-content: center;
            align-items: center;
            a {
              width: 165px;
              height: 45px;
              font: .875em/45px @sansregular;
              color: #bababa;
              text-align: center;
              text-decoration: none;
              border: 1px solid #bababa;
              border-radius: 5px;
              &:hover {
                color: #fff;
                background-color: #1b6b77;
              }
            }
          }
          .info {
            margin-left: 110px;
            width: 100%;
            font: 1em/1.125em @sansregular;
            color: #474747;
            span:nth-child(1) {
              font: 1.125em/1.25em @sansbold;
            }
          }
        }
      }
    }
  }
  .contentAddres {
    display: flex;
    iframe {
      width: 60%;
      height: 600px;
    }
    .contentAddres__contactForm {
      width: 40%;
      display: flex;
      flex-wrap: wrap;
      background-color: #176975;
      .comentary {
        margin: 45px 0 0 45px;
        input {
          padding-left: 15px;
          font: 1.125em/45px @sansregular;
          color: #828282;
          border: 1px solid #176975;
          border-radius: 10px;
          outline: none;
        }
        input[type*="text"] {
          margin: 0 5px 10px;
          width: 245px;
          height: 45px;
        }
        textarea {
          margin: 0 5px 10px;
          padding-left: 15px;
          width: 515px;
          height: 100px;
          font: 1.125em/45px @sansregular;
          color: #828282;
          border: 1px solid #176975;
          border-radius: 10px;
          outline: none;
          resize: none;
        }
        button {
          margin-top: 20px;
          width: 165px;
          height: 45px;
          font: 1.125em/40px @sansregular;
          color: #fff;
          text-align: center;
          background: transparent;
          border: 1px solid #fff;
          border-radius: 10px;
          outline: none;
          &:hover {
            cursor: pointer;
          }
        }
      }
      .contacts {
        margin-left: 45px;
        width: 70%;
        display: flex;
        justify-content: space-between;
        p {
          font: 1.125em/1.25em @sansregular;
          color: #fff;
          a {
            color: #fff;
            text-decoration: none;
            &:hover {
              text-decoration: underline;
            }
          }
        }
      }
      .social {
        margin-left: 45px;
        width: 70%;
        display: flex;
        justify-content: space-between;
        ul {
          display: flex;
          justify-content: space-between;
          a {
            margin: 0 5px;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 50px;
            height: 50px;
            font-size: 1.5em;
            color: #176975;
            text-decoration: none;
            background-color: #fff;
            border-radius: 75px;
          }
        }
        p {
          color: #fff;
          text-align: right;
          font: 1em/1.125em @sansbold;
          span:nth-child(1) {
            font: .875em/1em @sansregular;
          }
        }
      }
    }
  }
  ::-webkit-scrollbar{
    width: 10px;
  }
  ::-webkit-scrollbar-button{
    width: 10px;
    height: 0;
  }
  ::-webkit-scrollbar-thumb{
    width: 5px;
    border: 1px solid @linkColor;
    border-radius: 5px;
    background-color: @fontColor;
    box-shadow: 0 1px 1px #fff inset;
  }
  ::-webkit-scrollbar-track{
    width: 5px;
    background-color: @mainColor;
  }
  ::-webkit-scrollbar-track-piece{
    box-shadow: 0 1px 1px #000 inset;
  }
}